.allProduct {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
    margin-bottom: 50px;
    margin: 50px;
    padding-top: 20px;
  
    .product_title {
      text-align: center;
      h2 {
        font-size: 30px;
      }
      ul {
        display: flex;
        gap: 20px;
        list-style: none;
        padding: 20px;
        align-items: center;
        justify-content: center;
  
        li {
          a {
            text-decoration: none;
            color: rgb(24, 23, 23);
            font-size: 16px;
  
            &:hover {
              color: #ff4c58;
            }
          }
        }
      }
    }
  
    .container_product {
      display: flex;
      flex-wrap: wrap;
      gap: 30px;
      justify-content: center;
      align-items: center;
      margin-top: 50px;
      margin-bottom: 50px;
  
      .card_product {
        width: 300px;
        height: 65vh;
        background-color: white;
        border-radius: 10px;
        padding: 20px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
        position: relative;
  
        .imgBx {
          position: relative;
          img {
            /* width: 279px;
            height: 300px; */
            width: 100%;
            height: 300px;
            border-radius: 10px;
            object-fit: cover;
          }
        }
  
        .contentBx {
          h2 {
            font-size: 15px;
          }
          h3 {
            font-size: 20px;
            color: #ff4c58;
          }
  
          .rate {
            display: flex;
            gap: 5px;
            align-items: center;
  
            .rateIcon {
              display: flex;
              gap: 5px;
              align-items: center;
              color: orange;
            }
  
            .rating_num {
              font-size: 16px;
              color: gray;
            }
          }
        }
  
        .hov {
          display: flex;
          flex-direction: column;
          padding: 8px 20px;
          transition: 0.5s;
          opacity: 2;
          visibility: hidden;
          position: absolute;
  
          .icon-p {
            display: flex;
            justify-content: space-between;
            align-items: center;
            transition: 1s;
  
            ul {
              display: flex;
              gap: 25px;
              list-style: none;
  
              li {
                 background-color: #fff;
                text-align: center;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 20px;
                border-radius: 5px;
                padding: 10px;
                box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
                a {
                  text-decoration: none;
                  color: rgb(24, 23, 23);
  
                  &:hover {
                    background-color: #ff4c58;
                    color: #fff;
                    transition: 1s all ease-in;
                    -webkit-transition: 1s all ease-in;
                    -moz-transition: 1s all ease-in;
                    -ms-transition: 1s all ease-in;
                    -o-transition: 1s all ease-in;
                  }
                }
  
                &:hover {
                  background-color: #ff4c58;
                  color: #fff;
                  transition: 0.5s all ease-in;
                  -webkit-transition: 0.5s all ease-in;
                  -moz-transition: 0.5s all ease-in;
                  -ms-transition: 0.5s all ease-in;
                  -o-transition: 0.5s all ease-in;
                  margin-bottom: 11px;
                }
              }
            }
          }
  
          .color {
            display: flex;
            gap: 5px;
            align-items: start;
            justify-content: start;
            text-align: start;
            margin-top: 200px;
            transition: 1s;
            span {
              width: 20px;
              height: 20px;
  
              border-radius: 50%;
              margin: 0 5px;
              cursor: pointer;
              border: 1px solid rgba(0, 0, 0, 0.2);
              padding: 2px;
  
              &:nth-child(1) {
                background-color: #ff4c58;
              }
              &:nth-child(2) {
                background-color: #156983;
              }
              &:nth-child(3) {
                background-color: #c9a440;
              }
            }
          }
        }
      }
  
      .card_product:hover .hov {
        visibility: visible;
        margin-top: -250px;
        z-index: 100;
      }
      /* 
  .card_product imgBx:hover .hov {
    visibility: visible;
  background-color: #c9a440;
  margin-top: -150px;
  z-index: 100;
  } */
    }
  }
  
